
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
data() {
return {};
},
components: {
XHeader,
Group,
Cell
},
computed: {
addBtnShow() {
return (
(this.$route.query.data || []).filter(
v => v.CustomerName === this.$route.query.cusname
).length === 0
);







}
},
filters: {
isRe(r) {
return r ? "已报备" : "未报备";
}
},
methods: {
goBack() {
this.$router.push({
path: "/custrepor"
});
this.$parent.childrenBack();
},
toBacks(val) {
this.$router.push({
path: "/custrepor",
query: {
cutItems: val || null
}
});
this.$parent.childrenItemClick()
},
addCus() {
this.$router.push({
path: "/custrepor"
});
}
}
};



